<template>
  <div class="c_app_manage">
    <CContentHeader content="应用管理" />

    <!-- tabbar -->
    <div class="cpi_tabbar flex">
      <div class="cpi_tabbar_list flex">
        <p v-for="item in platformList" :key="item._id">
          <router-link :class="{active: (platformSearch == item.alias)}" :to="'/appmanages/' + item.alias">{{item._name}}</router-link>
        </p>
      </div>
      <!-- <p>
        <router-link active-class="active" to="/appmanage/h5">H5应用</router-link>
      </p>
      <p>
        <router-link active-class="active" to="/appmanage/small">小程序</router-link>
      </p>
      <p>
        <router-link active-class="active" to="/appmanage/Ios">IOS应用</router-link>
      </p>
      <p>
        <router-link active-class="active" to="/appmanage/andriod">Andriod应用</router-link>
      </p> -->
      <div class="cpi_tabbar_new">
        <el-button type="primary" @click="addNewApp">新建应用</el-button>
      </div>
    </div>

    <div class="cpi_cover">
      <el-dialog title="选择要新建的应用类型" :visible.sync="dialogVisible" width="30%">
        <!-- <span></span> -->
        <div class="cpi_cover_btn flex">
          <div class="cpi_cover_btn_item" @click="addAndriod">Andriod</div>
          <div class="cpi_cover_btn_item" @click="addIos">IOS</div>
          <div class="cpi_cover_btn_item" @click="addH5">H5应用</div>
          <div class="cpi_cover_btn_item" @click="addSmall">小程序</div>
        </div>
      </el-dialog>
    </div>

    <div>
      <router-view :search="platformSearch"></router-view>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      platformSearch: '', // 路由上查询的参数
      platformList: [],
      dialogVisible: false, // 是否显示弹出层
    }
  },
  methods: {
    // 创建新应用
    addNewApp() {
      console.log('点击创建新应用');
      this.dialogVisible = true;
    },

    /* 创建几种应用 start */
    addAndriod() {
      console.log('新建Andriod');
      this.$router.push('/appmanage/createAndriod');
    },
    addIos() {
      console.log('新建Ios');
      this.$router.push('/appmanage/createIos');
    },
    addH5() {
      console.log('新建H5');
      this.$router.push('/appmanage/createH5');
    },
    addSmall() {
      console.log('新建小程序');
      this.$router.push('/appmanage/createSmall');
    },
    /* 创建几种应用 end */

    // 获取平台
    async getPlatform() {
      let company_id = this.$zero.getCompany_id();
      if (!company_id) {
        return this.$zero.ToastError('无效的company_id,请重新登录');
      }
      let platformList = await this.$api.application.getPlatformList({
        company_id: company_id
      });

      if (platformList.statusCode === 75500) {
        return this.$zero.ToastError('服务繁忙，请稍后再试');
      }
      if (platformList.statusCode !== 75200) {
        return this.$zero.ToastError(`请求平台列表错误，错误码${platformList.statusCode}`);
      }
      // 请求成功了
      console.log('结果');
      console.log(platformList);
      this.platformList = platformList.platforms;
      console.log(this.$router);
      // this.$router.replace('/appmanage/' + this.platformList[0].alias);
    },

    // 分析参数
    analysis_urlQuery() {
      // 根据响应到的内容查询内容
      console.log('监听');
      let platform = this.$route.path.split('/appmanages/')[1];
      this.platformSearch = platform;
    }
  },

  watch: {
    // 监听路由的变化
    '$route.path': function (newVal, oldVal) {
      // console.log('可以监听到变化');
      this.analysis_urlQuery();
    }
  },

  created() {
    this.getPlatform();
    this.analysis_urlQuery();
  }

};
</script>
<style scoped>
.cpi_tabbar{
  /* width: 100%; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #fff;
}
.cpi_tabbar_list {
  height: 45px;
  line-height: 45px;
  font-size: 14px;
}
.cpi_tabbar_list > p {
  margin: 0 25px;
}
.cpi_tabbar_list > p a {
  padding: 0 10px;
  text-decoration: none;
  color: #000;
}
.cpi_tabbar_list > p a.active {
  box-sizing: border-box;
  display: block;
  height: 45px;
  border-bottom: 2px solid #347bef;
}
.cpi_tabbar_new{
  margin-right: 40px;
}
.cpi_cover_btn {
  justify-content: space-around;
}
.cpi_cover_btn_item {
  cursor: pointer;
}
</style>
